export default {
	data() {
		return {
			userInfo: {},
			hasPhone: false,
			bindPhone: false,
			isHide: false,
		}
	},
	onLoad() { 
		this.wxGetUserInfo()
	},
	methods: {
		wxGetUserInfo: function(res) {
			console.log(res);
			if (!res.detail.iv) {
				uni.showToast({
					title: "您取消了授权,登录失败",
					icon: "none"
				});
				return false;
			}
			console.log(res.detail);
			this.userInfo = res.detail.userInfo;
		},
		enterUserInfo() {
			uni.navigateTo({
				url: 'userInfo'
			})
		},
		enterMessage() {
			uni.navigateTo({
				url: 'message'
			})
		},
		enterChangePhone() {
			if (this.hasPhone) {
				uni.navigateTo({
					url: 'changePhone'
				})
			} else {
				this.isHide = true;
			}
		},
		close(){
			this.isHide = false;
		}
	}
}
